.App {
  text-align: center;
}

body {
  padding: 0;
}

.mapBox {
  width: 700px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: -2px 4px 10px 6px rgba(0, 0, 0, 0.25);
}

.mapBox_control {
  display: flex;
  padding: 20px 20px;
  justify-content: space-evenly;
}

.mapBox_control > .btn {
  box-shadow: 1px 2px 4px 2px rgba(0, 0, 0, 0.25);
}

.box_modalControl {
  display: flex;
  padding: 10px 10px;
  justify-content: space-evenly;
}

.mapDisplayWrapper {
  margin: 20px 0;
  justify-content: space-evenly;
  box-shadow: 2px 2px 4px 2px rgba(0, 0, 0,0.25);
}

.mapDisplay {
  width: 350px;
  margin: 10px auto;
}

.mapDisplay_image {
  width: 300px;
  height: 300px;
  padding: 10px 0;
}

.mapDisplay > h5 {
  padding: 20px auto;
}

.mapDisplay_control {
  display: flex;
  justify-content: space-evenly;
  padding: 10px;
}

.collection {
  margin-top: 60px;
}